<template>
    <div class="field-monitor">
        <div class="game-nav">
            <div :class="['nav-item', activeGameType == item.key ? 'active' : '']" v-for="(item, index) in gameBigList" :key="'game-type-' + index" @click="handleSelectGameBigType(item)">
                <span>{{item.title}}</span>
            </div>
        </div>
        <div class="content">
            <div class="query-list">
                <div class="query-list-t">
                    <i-select v-model="query.gameType" @on-change="handleChangeType">
                        <i-option value="0">所有游戏</i-option>
                        <i-option v-for="(item, index) in gameList" :key="index" :value="item.type">{{item.name}}</i-option>
                    </i-select>
                    <i-select v-model="query.orderStatus" @on-change="handleChangeType">
                        <i-option v-for="(item, index) in orderList" :key="index" :value="item.key">{{item.title}}</i-option>
                    </i-select>
                    <i-select v-model="query.selectUserType" @on-change="handleChangeType">
                        <i-option v-for="(item, index) in userTypeList" :key="index" :value="item.value">{{item.title}}</i-option>
                    </i-select>
                    <i-input placeholder="查询用户ID/用户名" class="query-input-item" v-model="query.searchUsername"></i-input>
                </div>
                <div class="query-list-t">
                    <div class="quick-method">
                        <span :class="quickSelect === 1 ? 'active' : ''" @click="handleQueryByDate(1)">今天</span>
                        <span :class="quickSelect === 2 ? 'active' : ''" @click="handleQueryByDate(2)">昨天</span>
                        <span :class="quickSelect === 3 ? 'active' : ''" @click="handleQueryByDate(3)">近一周</span>
                        <span :class="quickSelect === 4 ? 'active' : ''" @click="handleQueryByDate(4)">自定义</span>
                    </div>
                    <div class="quick-date">
                        <span>区间：</span>
                        <DatePicker v-model="query.startTime" :disabled="canEdit" type="date" class="date-item"></DatePicker>
                        <span>06:00 --</span>
                        <DatePicker v-model="query.endTime" :disabled="canEdit" type="date" class="date-item"></DatePicker>
                        <span>06:00</span>
                    </div>
                    <i-input placeholder="查询期号" class="query-input-item" v-model="query.perids"></i-input>
                </div>
            </div>
            <div class="query-operate">
                <span @click="handleChangeType">查询</span>
                <span @click="agree.show = true;">撤销未结注单</span>
            </div>
            <div class="query-poptip">
                <span>说明：撤销未结注单流程为先条件查询再撤销，撤销的目标是以列表内显示的查询结果中未开奖的注单为准。</span>
            </div>
        </div>
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>注单列表</span>
                    <div class="list-p">
                        <div class="list-flush-time">
                            <span v-show="flushTime > 0" v-text="`下次注单刷新：${flushTime}秒`"></span>
                            <span v-show="flushTime == 0">刷新中</span>
                            <div class="button" @click="flush.show = true">立即刷新</div>
                        </div>
                        <div class="list-poptip">
                            <span>下注合计：</span>
                            <span>{{allBet}}</span>
                            <span>盈亏合计：</span>
                            <span>{{lossAndWinner}}</span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="list-data">
                <div class="data-header">
                    <p class="item-info item-1">用户名</p>
                    <p class="item-info item-2">ID</p>
                    <p class="item-info item-3">游戏</p>
                    <p class="item-info item-4">期号</p>
                    <p class="item-info item-5">下注内容</p>
                    <p class="item-info item-6">使用积分</p>
                    <p class="item-info item-7">下注时间</p>
                    <p class="item-info item-8">状态</p>
                    <p class="item-info item-9">盈亏</p>
                </div>
                <div class="data-con">
                    <div class="data-item" v-for="(item, index) in dataList" :key="index">
                        <p class="item-info item-1">{{item.username}}</p>
                        <p class="item-info item-2">{{item.showId}}</p>
                        <p class="item-info item-3">{{item.gameName}}</p>
                        <p class="item-info item-4">{{item.perids}}</p>
                        <p class="item-info item-5" :title="item.currentBet">{{item.currentBet}}</p>
                        <p class="item-info item-6">{{item.bet}}</p>
                        <p class="item-info item-7">{{item.time}}</p>
                        <p class="item-info item-8">{{gameList.find(ele => ele.name == item.gameName).value > item.perids && item.status === '未开奖' ? '未结算' : item.status}}</p>
                        <p class="item-info item-9">
                            <span v-show="item.status !== '未开奖'">{{item.lastProLoss}}</span>
                            <!-- <span v-show="gameList.find(ele => ele.name == item.gameName).nextPeriods <= item.perids || item.status !== '未开奖'">{{item.lastProLoss}}</span> -->
                            <!-- <span class="manual-lottery" @click="handleManualLottery(item.gameName, item.perids)" v-show="gameList.find(ele => ele.name == item.gameName).nextPeriods > item.perids && item.status === '未开奖'">手动结算</span> -->
                        </p>
                    </div>
                </div>
            </div>
            <Page class="footer-page" :total="page.total" :page-size="page.pageSize" :current="page.pageNum" show-sizer @on-page-size-change="handleChangeSize" @on-change="handleChangePage"/>
        </div>
        <!-- <loading :loading="showLoading"></loading> -->
        <Modal v-model="agree.show" :styles="{top: '200px'}" width="300">
            <p slot="header" style="color: #0d1941;font-size: 16px;text-align:center">
                <span>撤销未结注单</span>
            </p>
            <p style="text-align:center">是否确认撤销未结注单？</p>
            <div slot="footer" class="modal-footer">
                <div class="enter-cancel" @click="agree.show = false">取消</div>
                <div class="enter-ok" @click="handleEnterCancel">确定</div>
            </div>
        </Modal>
        <Modal v-model="flush.show" :styles="{top: '200px'}" width="300">
            <p slot="header" style="color: #0d1941;font-size: 16px;text-align:center">
                <span>立即刷新</span>
            </p>
            <p style="text-align:center">是否确定刷新注单列表？</p>
            <div slot="footer" class="modal-footer">
                <div class="enter-cancel" @click="flush.show = false">取消</div>
                <div class="enter-ok" @click="handleChangeType('now')">确定</div>
            </div>
        </Modal>
    </div>
</template>

<script>
import Bus from '../../tools/eventBus/index.js';

export default {
    data() {
        return {
            activeGameType: 'lottery',
            gameBigList: [
                {
                    title: '彩票游戏',
                    key: 'lottery'
                },
                {
                    title: '视讯游戏',
                    key: 'video'
                }
            ],
            flush: {
                show: false,
                timeout: null,
                reCreate: false
            },
            agree: {
                show: false
            },
            showLoading: false,
            canEdit: true,
            dataList: [],
            query: {
                startTime: new Date().getHours() >= 6 ? new Date() : new Date(new Date().setDate(new Date().getDate() - 1)),
                endTime: new Date(new Date().setDate(new Date().getDate() + 1)),
                searchUsername: '',
                gameType: '0',
                orderStatus: 1,
                selectUserType: '1',
                perids: ''
            },
            quickSelect: 1,
            allBet: 0,
            lossAndWinner: 0,
            gameList: [],
            orderList: [
                {
                    key: 1,
                    title: '所有注单'
                },
                {
                    key: 2,
                    title: '未开奖注单'
                },
                {
                    key: 3,
                    title: '已开奖注单'
                }
            ],
            userTypeList: [
                {
                    title: '正式用户',
                    value: '1'
                },
                {
                    title: '虚假用户',
                    value: '4'
                }
            ],
            statusTextList: ['', '等待中', '封盘中', '开奖中', '已停售', '未开奖', '已关闭'],
            page: {
                total: 0,
                pageSize: 10,
                pageNum: 1
            },
            auto: null,
            isLoading: false,
            flushTime: 0,
            lastFlushTime: 0
        }
    },
    beforeRouteEnter (to, from, next) {
        // ...
        next(vm => {
            vm.getGameList();
            vm.autoFlush();
            setInterval(function() {
                if (vm.flushTime != 0) {
                    vm.flushTime--;
                }
            }, 1000)
        })
    },
    beforeRouteLeave (to, from, next) {
        // ...
        let that = this;

        that.auto = null;
        next();
    },
    methods: {
        autoFlush() {
            let that = this;
            that.auto = function() {
                that.flushTime = 0;
                that.handleAllInfo().then(function() {
                    that.handleGetList().then(function() {
                        that.flushTime = 15;
                        that.flush.timeout = setTimeout(function() {
                            that.auto && that.auto();
                        }, 15000)
                    }).catch(function() {
                        that.flushTime = 15;
                        that.flush.timeout = setTimeout(function() {
                            that.auto && that.auto();
                        }, 15000)
                    })
                })
            }
            that.auto();
        },
        handleSelectGameBigType(item) {
            let that = this,
                curTime = +new Date();
            that.flush.show = false;
            if (!that.lastFlushTime || curTime - that.lastFlushTime > 5000) {
                clearTimeout(that.flush.timeout);
                that.flush.timeout = null;
                that.flushTime = 0;
                that.auto = null;
                that.lastFlushTime = curTime;
                that.page.pageNum = 1;
                that.query.gameType = '0';
                that.activeGameType = item.key;
                that.page.pageNum = 1;
                that.getGameList();
                that.autoFlush();
            } else {
                that.$Message.error('刷新间隔需要大于5秒');
            }
        },
        getGameList() {
            let that = this;

            that.gameList = [];
            if (that.activeGameType == 'lottery') {
                that.$axios({
                    url: '/api/Merchant/GetGameList'
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.gameList = result.map(item => {
                            return {
                                name: item.GameName,
                                type: item.GameType,
                                key: item.NickName,
                                nextPeriods: ''
                            }
                        }).sort((a, b) => a.type - b.type);
                    }
                })
            } else {
                that.gameList.push({
                    name: '百家乐',
                    type: 1,
                    key: 'bjl',
                    nextPeriods: ''
                })
            }
        },
        handleEnterCancel() {
            let that = this;

            that.agree.show = false;
            that.handleRevokeOrder();
        },
        handleManualLottery(key, perids) {
            let that = this;

            Bus.$emit('changeActive', 'other');
            that.$router.replace({name: 'manualLottery', params: {type: that.gameList.find(item => item.name == key).type, periods: perids}});
        },
        handleQueryByDate(type) {
            let that = this,
                dateNow = new Date().getHours() >= 6 ? new Date() : new Date(new Date().setDate(new Date().getDate() - 1)),
                afterDate = new Date(new Date().setDate(new Date().getDate() + 1)),
                beforeDate = new Date(new Date().setDate(new Date().getDate() - 1)),
                beforeSeven = new Date(new Date().setDate(new Date().getDate() - 6));

            that.quickSelect = type;
            switch(type) {
                case 1:
                    that.canEdit = true;
                    that.query.startTime = dateNow;
                    that.query.endTime = afterDate;
                    that.handleGetList(dateNow.pattern('yyyy-MM-dd') + ' 06:00', afterDate.pattern('yyyy-MM-dd') + ' 06:00');
                    that.handleAllInfo(dateNow.pattern('yyyy-MM-dd') + ' 06:00', afterDate.pattern('yyyy-MM-dd') + ' 06:00');
                break;
                case 2:
                    that.canEdit = true;
                    that.query.startTime = beforeDate;
                    that.query.endTime = dateNow;
                    that.handleGetList(beforeDate.pattern('yyyy-MM-dd') + ' 06:00', dateNow.pattern('yyyy-MM-dd') + ' 06:00');
                    that.handleAllInfo(beforeDate.pattern('yyyy-MM-dd') + ' 06:00', dateNow.pattern('yyyy-MM-dd') + ' 06:00');
                break;
                case 3:
                    that.canEdit = true;
                    that.query.startTime = beforeSeven;
                    that.query.endTime = afterDate;
                    that.handleGetList(beforeSeven.pattern('yyyy-MM-dd') + ' 06:00', afterDate.pattern('yyyy-MM-dd') + ' 06:00');
                    that.handleAllInfo(beforeSeven.pattern('yyyy-MM-dd') + ' 06:00', afterDate.pattern('yyyy-MM-dd') + ' 06:00');
                break;
                case 4:
                    that.canEdit = false;
                break;
            }
        },
        handleChangeSize(_pageSize) {
            let that = this;

            that.page.pageSize = _pageSize;
            that.page.pageNum = 1;
            that.handleGetList();
        },
        handleChangePage(page) {
            let that = this;

            that.page.pageNum = page;
            that.handleGetList();
        },
        handleChangeType(type) {
            let that = this,
                curTime = +new Date();
            that.flush.show = false;
            if (!that.lastFlushTime || curTime - that.lastFlushTime > 5000) {
                if (type == 'now') {
                    clearTimeout(that.flush.timeout);
                    that.flush.timeout = null;
                    that.flushTime = 0;
                    that.auto = null;
                    that.flush.reCreate = true;
                }
                that.lastFlushTime = curTime;
                that.page.pageNum = 1;
                that.handleAllInfo().then(function() {
                    that.handleGetList().then(function() {
                        if (that.flush.reCreate) {
                            that.flush.reCreate = false;
                            that.autoFlush();
                        }
                    })
                })
            } else {
                that.$Message.error('刷新间隔需要大于5秒');
            }
        },
        handleRevokeOrder() {
            let that = this,
                vParam = {},
                url_ = '';

            if (that.query.gameType != '0') {
                vParam['gameType'] = that.query.gameType;
            }
            vParam['betType'] = 2;

            if (that.query.selectUserType) {
                vParam['userType'] = that.query.selectUserType;
            }

            if (that.query.searchUsername) {
                vParam['userKeyword'] = that.query.searchUsername;
            }

            if (!that.query.startTime && !startTime) {
                that.$Message.error('请选择开始时间');
                return;
            }

            if (!that.query.endTime && !endTime) {
                that.$Message.error('请选择结束时间');
                return;
            }

            vParam['startTime'] = that.query.startTime.pattern('yyyy-MM-dd') + ' 06:00';
            vParam['endTime'] = that.query.endTime.pattern('yyyy-MM-dd') + ' 06:00';

            if (that.query.perids) {
                vParam['nper'] = that.query.perids;
            }
            url_ = that.activeGameType == 'lottery' ? '/api/Monito/RevokeNoteList' : '/api/Monito/RevokeVideoNoteList';
            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: url_,
                    method: 'put',
                    params: vParam
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                    that.handleGetList();
                })
            }
        },
        handleAllInfo(startTime, endTime) {
            let that = this,
                vParam = {},
                url_ = '';

            if (that.query.gameType != '0') {
                vParam['gameType'] = that.query.gameType;
            }
            vParam['betType'] = that.query.orderStatus;

            if (that.query.selectUserType) {
                vParam['userType'] = that.query.selectUserType;
            }

            if (that.query.searchUsername) {
                vParam['userKeyword'] = that.query.searchUsername;
            }

            if (!that.query.startTime && !startTime) {
                that.$Message.error('请选择开始时间');
                return;
            }

            if (!that.query.endTime && !endTime) {
                that.$Message.error('请选择结束时间');
                return;
            }

            vParam['startTime'] = startTime || that.query.startTime.pattern('yyyy-MM-dd') + ' 06:00';
            vParam['endTime'] = endTime || that.query.endTime.pattern('yyyy-MM-dd') + ' 06:00';
            if (that.query.perids) {
                vParam['nper'] = that.query.perids;
            }
            url_ = that.activeGameType == 'lottery' ? '/api/Monito/GetProfitLossInfo' : '/api/Monito/GetVideoProfitLossInfo';

            return new Promise((resolve, reject) => {
                that.$axios({
                    url: url_,
                    params: vParam
                }).then(data => {
                    if (data.data.Status === 100) {
                        let result = data.data.Model;

                        that.allBet = result.Flow;
                        that.lossAndWinner = result.ProLoss;
                        // that.gameList.forEach(item => {
                        //     item.nextPeriods = result[item.key];
                        // })
                    }
                    resolve();
                })
            })
        },
        handleGetList(startTime, endTime) {
            let that = this,
                vParam = {},
                url_ = '';

            if (that.query.gameType != '0') {
                vParam['gameType'] = that.query.gameType;
            }
            vParam['betType'] = that.query.orderStatus;

            if (that.query.selectUserType) {
                vParam['userType'] = that.query.selectUserType;
            }

            if (that.query.searchUsername) {
                vParam['userKeyword'] = that.query.searchUsername;
            }

            if (!that.query.startTime && !startTime) {
                that.$Message.error('请选择开始时间');
                return;
            }

            if (!that.query.endTime && !endTime) {
                that.$Message.error('请选择结束时间');
                return;
            }

            vParam['startTime'] = startTime || that.query.startTime.pattern('yyyy-MM-dd') + ' 06:00';
            vParam['endTime'] = endTime || that.query.endTime.pattern('yyyy-MM-dd') + ' 06:00';

            if (that.query.perids) {
                vParam['nper'] = that.query.perids;
            }

            vParam['start'] = that.page.pageNum;
            vParam['pageSize'] = that.page.pageSize;
            url_ = that.activeGameType == 'lottery' ? '/api/Monito/GetProfitLossByType' : '/api/Monito/GetVideoProfitLossByType';

            return new Promise((resolve, reject) => {
                if (!that.showLoading) {
                    that.showLoading = true;
                    that.$axios({
                        url: url_,
                        params: vParam
                    }).then(data => {
                        if (data.data.Status === 100) {
                            let result = data.data.Data;

                            that.page.total = data.data.Total;
                            that.dataList = result.map(item => {
                                return {
                                    username: item.NickName,
                                    showId: item.OnlyCode,
                                    gameName: item.GameName,
                                    perids: item.Nper,
                                    currentBet: item.BetContent,
                                    bet: item.UserBetMoney,
                                    time: item.BetTime,
                                    status: item.Status,
                                    lastProLoss: item.Status !== '未开奖' ? item.ProLoss : '-'
                                }
                            })
                        }
                        that.showLoading = false;
                        resolve();
                    })
                } else {
                    that.$Message.error('自动刷新中请稍后');
                    reject();
                }
            });
        }
    },
}
</script>

<style lang="scss" scoped>
.field-monitor {
    position: relative;
    .game-nav {
        display: flex;
        align-items: stretch;
        margin: 10px 0;
        height: 36px;
        background-color: #f2f2f2;
        border-radius: 4px;
        overflow: hidden;

        .nav-item {
            padding: 0 20px;

            line-height: 36px;

            cursor: pointer;
        }

        .active {
            background-color: rgb(65, 199, 219);
        }
    }
    .content {
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        .query-list {
            padding: 10px;

            background-color: rgb(228, 228, 228);
            border: 1px solid rgb(215, 215, 215);

            .query-list-t {
                display: flex;
                align-items: center;

                margin-top: 14px;

                & > * {
                    margin-right: 13px;

                    text-align: left;
                }

                .query-input-item {
                    flex-shrink: 0;

                    margin-right: 0;

                    width: 135px;
                }

                .quick-method {
                    display: flex;
                    align-items: center;

                    width: 270px;
                    height: 30px;
                    line-height: 30px;

                    border: solid 1px rgb(174, 174, 174);
                    text-align: center;

                    & > span {
                        flex: 1 1 25%;

                        background-color: #fff;
                        cursor: pointer;
                    }

                    .active {
                        background-color: rgb(65, 199, 219);
                    }
                }

                .quick-date {
                    .date-item {
                        width: 110px;
                    }
                }
            }

            .query-list-t:first-child {
                margin-top: 0;
            }
        }

        .query-operate {
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            margin-left: 13px;

            height: 100px;

            & > span {
                display: inline-block;

                color: #fff;
                cursor: pointer;
            }

            & > span:first-child {
                width: 110px;
                height: 50px;
                line-height: 47px;

                // background-color: rgb(51, 204, 153);
                // border-radius: 4px;
                // border: solid 1px rgb(161, 161, 161);
                background-color: #0078ff;
                box-shadow: inset 0px 0px 5px 0px rgba(124, 183, 255, 0.75);
                border-radius: 10px;
                border: solid 1px #0e3c90;
            }

            & > span:last-child {
                width: 110px;
                height: 33px;
                line-height: 30px;

                // background-color: rgb(255, 153, 153);
                // border-radius: 4px;
                // border: solid 1px rgb(255, 102, 153);
                background-color: #f73152;
                box-shadow: inset 0px 0px 5px 0px rgba(255, 124, 124, 0.75);
                border-radius: 10px;
                border: solid 1px #900e0e;
            }
        }

        .query-poptip {
            height: 26px;
            line-height: 32px;

            color: rgb(255, 102, 0);
        }

        .list-header {
            width: 100%;

            .list-title {
                justify-content: space-between;

                width: 100%;

                .list-p {
                    display: flex;
                    .list-flush-time {
                        display: flex;
                        margin-right: 12px;
                        align-items: center;

                        .button {
                            margin-left: 12px;
                            width: 109px;
                            height: 28px;
                            line-height: 24px;
                            background-color: #0078ff;
                            -webkit-box-shadow: inset 0px 0px 5px 0px rgba(124, 183, 255, 0.75);
                            box-shadow: inset 0px 0px 5px 0px rgba(124, 183, 255, 0.75);
                            border-radius: 10px;
                            border: solid 1px #0e3c90;
                            text-align: center;
                            cursor: pointer;
                            color: #fff;
                        }
                    }
                    .list-poptip {
                        width: 400px;
                        height: 30px;
                        line-height: 30px;

                        border-radius: 2px;
                        background-color: rgb(228, 228, 228);
                        border: 1px solid rgb(201, 201, 201);

                        & > span {
                            display: inline-block;

                            font-weight: 400;
                        }

                        & > span:nth-child(2n+1) {
                            width: 110px;

                            text-align: right;
                        }

                        & > span:nth-child(2n+2) {
                            width: 85px;

                            color: rgb(51, 153, 102);
                        }
                    }
                }
            }
        }

